import React, { Component } from 'react';
import './App.css';
import Todo from './Todo'
import styled from 'styled-components';

const Wrapper = styled.div`
display: flex;
align-items: center;
flex-direction: column;
`

const Title = styled.h1`
margin-top: 30px;
`

const Add = styled.div`
display: flex;
margin: 30px 0px;
`

const AddBtn = styled.button`
position: relative;
left: 20px;
@media only screen and (max-width: 600px){
  left: 2px;
}
`
const List = styled.div`
margin: 30px 0px;
`

const FiltersAll = styled.div`
display: inline;
`

const FiltersCompleted = styled.div`
display: inline;
`

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      todos: [],
      todoText: ''
    }
    this.id = 1
    this.handleChange = this.handleChange.bind(this)
    this.addTodo = this.addTodo.bind(this)
    this.deleteTodo = this.deleteTodo.bind(this)
    this.markTodo = this.markTodo.bind(this)
  }
  componentDidUpdate(prevProps, prevState) {  //state有變就把todos存到本地
    if (prevState.todos !== this.state.todos) {
      window.localStorage.setItem('todolist', JSON.stringify(this.state.todos))
    }
  }

  componentDidMount() {  //初始化時拿出本地值渲染
    const todoData = window.localStorage.getItem('todolist')
    const oldTodo = JSON.parse(todoData)
    if (todoData) {
      this.setState({
        todos: oldTodo,
      })
      this.id = oldTodo[oldTodo.length - 1].id + 1

    }
  }

  handleChange(e) {
    this.setState(
      { todoText: e.target.value }
    )
  }
  addTodo() {
    const { todoText, todos } = this.state
    this.setState({
      todos: [...todos, {
        id: this.id,
        isCompleted: false,
        text: todoText,
        filter: 'all'
      }], todoText: ''
    })
    this.id++
  }
  deleteTodo(id) {
    this.setState({
      todos: this.state.todos.filter(todo => todo.id !== id)
    })
  }

  markTodo(id) {
    this.setState({
      todos: this.state.todos.map(todo => {
        if (todo.id !== id) {
          return todo
        }

        return {
          ...todo,
          isCompleted: !todo.isCompleted
        }
      })
    })
  }


  render() {
    const { todos, todoText, filter } = this.state
    return (
      <Wrapper className="wrapper">
        <Title>Simple TodoList</Title>
        <Add className="add">
          <input type="text" name="todo" className="form-control" value={todoText} onChange={this.handleChange} />
          <AddBtn className="btn btn-primary add-btn" onClick={this.addTodo}>ADD</AddBtn>
        </Add>
        <div className="filters">
          <FiltersAll className="filters__all btn-outline-secondary btn-lg" onClick={() => {
            this.setState({
              filter: 'all'
            })
          }}>All</FiltersAll>
          <FiltersCompleted className="filters__completed btn-outline-secondary btn-lg" onClick={() => {
            this.setState({
              filter: 'completed'
            })
          }}>FINISHED</FiltersCompleted>
        </div>
        <List className="list">
          {todos
            .filter(todo => filter === 'completed' ? todo.isCompleted : true)
            .map(todo => (<Todo key={todo.id} todo={todo} deleteTodo={this.deleteTodo}
              markTodo={this.markTodo} />))}
        </List>
      </Wrapper>
    )
  }
}




export default App;
