import React, { Component } from 'react';
import styled from 'styled-components';
import './App.css';


class Todo extends Component {
    constructor(props) {
        super(props)
        this.delete = this.delete.bind(this)
        this.mark = this.mark.bind(this)
    }
    delete() {
        const { todo, deleteTodo } = this.props
        deleteTodo(todo.id)
    }
    mark() {
        const { todo, markTodo } = this.props
        markTodo(todo.id)
    }
    render() {
        const { todo } = this.props
        return (
            <div className={`list-item ${todo.isCompleted ? 'completed' : ''}`}>
                <div className="list-item__state" >
                </div>
                <div className="list-item__content">
                    {todo.text}
                </div>
                <div className="list-item__action">
                    <button className="list-item__delete btn btn-danger" onClick={this.delete}>Delete</button>
                    <button className={`list-item__mark ${todo.isCompleted ? 'btn btn-secondary' : 'btn btn-success'}`} onClick={this.mark}>{todo.isCompleted ? 'Undo' : 'Finish'}</button>
                </div>
            </div>
        )
    }
}

export default Todo;
